
.productos {
    .contenedor_botones {
        display: flex;
    }

    .P_ver,
    .P_actualizar,
    .P_eliminar {
        max-width: 3.5rem;
    }


}

.productos_clientes {
    .nombre_pagina{
        font-size: calc(20 + 5);
    }
    .titulo {
        margin-top: 1rem;
        h3{
            font-size: calc(15 + 5);
        }
    }
    
    .imagen_productos {
        display: block;

        .imagen1,
        .imagen2,
        .imagen3,
        .imagen4 {
            width: 7rem;
            height: 7rem;
        }
    }
    .video_producto {
        width: 7rem;
        height: 7rem;
    }
    .texto {
        padding: 1rem;
    }

    .modal {
        display: none;
    }
}