@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.tabla{
    margin: 0;
    padding: 0;

    @include m.tablet{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
    }

    .seleccion{
        border: 1px solid c.$light-blue-500;
        border-radius: 0 0 3rem;
        margin: 1rem 0;

        img{
            width: 100%;
            height: 22rem;
            margin: 0;
        }
        h4{
            color: c.$blanco;
            background-color: c.$light-blue-500;
            padding: 1rem 0 1rem 1rem;
            margin: 0 0 1rem 0;
        }
    }

}